'use client';

import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { toast } from 'sonner';
import { Eye, EyeOff, Loader2 } from 'lucide-react';

import { authClient } from '@/lib/auth-client';
import { enhancedPasswordSchema } from '@/lib/validations/password';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { PasswordStrengthIndicator } from '@/components/ui/password-strength-indicator';

/**
 * Form validation schema for password change
 */
const changePasswordSchema = z
  .object({
    currentPassword: z.string().min(1, 'ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'),
    newPassword: enhancedPasswordSchema,
    confirmPassword: z.string().min(1, 'ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'),
  })
  .refine((data) => data.newPassword === data.confirmPassword, {
    message: 'ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§',
    path: ['confirmPassword'],
  })
  .refine((data) => data.currentPassword !== data.newPassword, {
    message: 'ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ Îã¨ÎùºÏïº Ìï©ÎãàÎã§',
    path: ['newPassword'],
  });

type ChangePasswordFormData = z.infer<typeof changePasswordSchema>;

export interface ChangePasswordFormProps {
  onSuccess?: () => void;
  onCancel?: () => void;
}

/**
 * ChangePasswordForm Component
 *
 * Allows authenticated users to change their password with comprehensive validation
 * and security features including:
 * - Current password verification
 * - Real-time password strength indicator
 * - Enhanced password validation
 * - Automatic session revocation on other devices
 *
 * @example
 * ```tsx
 * <ChangePasswordForm onSuccess={() => console.log('Password changed!')} />
 * ```
 */
export function ChangePasswordForm({ onSuccess, onCancel }: ChangePasswordFormProps) {
  const [showCurrentPassword, setShowCurrentPassword] = React.useState(false);
  const [showNewPassword, setShowNewPassword] = React.useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = React.useState(false);

  const {
    register,
    handleSubmit,
    watch,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<ChangePasswordFormData>({
    resolver: zodResolver(changePasswordSchema),
    mode: 'onChange',
  });

  // Watch new password for strength indicator
  const newPassword = watch('newPassword', '');

  const onSubmit = async (data: ChangePasswordFormData) => {
    try {
      // Call better-auth changePassword API
      const { error } = await authClient.changePassword({
        currentPassword: data.currentPassword,
        newPassword: data.newPassword,
        revokeOtherSessions: true, // Security: logout all other devices
      });

      if (error) {
        // Handle better-auth errors
        if (error.message?.includes('current password')) {
          toast.error('ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§');
        } else if (error.message?.includes('same')) {
          toast.error('ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ Îã¨ÎùºÏïº Ìï©ÎãàÎã§');
        } else {
          toast.error(error.message || 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤ΩÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§');
        }
        return;
      }

      // Success!
      toast.success('ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§', {
        description: 'Î≥¥ÏïàÏùÑ ÏúÑÌï¥ Îã§Î•∏ Í∏∞Í∏∞ÏóêÏÑú ÏûêÎèô Î°úÍ∑∏ÏïÑÏõÉÎêòÏóàÏäµÎãàÎã§',
      });

      // Reset form
      reset();

      // Call success callback
      onSuccess?.();
    } catch (error) {
      console.error('Password change error:', error);
      toast.error('ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§');
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
      {/* Current Password */}
      <div className="space-y-2">
        <Label htmlFor="currentPassword">ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏</Label>
        <div className="relative">
          <Input
            id="currentPassword"
            type={showCurrentPassword ? 'text' : 'password'}
            autoComplete="current-password"
            placeholder="ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            {...register('currentPassword')}
            aria-invalid={!!errors.currentPassword}
            className="pr-10"
          />
          <button
            type="button"
            onClick={() => setShowCurrentPassword(!showCurrentPassword)}
            className="text-muted-foreground hover:text-foreground absolute top-1/2 right-3 -translate-y-1/2 transition-colors"
            aria-label={showCurrentPassword ? 'ÎπÑÎ∞ÄÎ≤àÌò∏ Ïà®Í∏∞Í∏∞' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥¥Í∏∞'}
          >
            {showCurrentPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
          </button>
        </div>
        {errors.currentPassword && (
          <p className="text-destructive text-sm">{errors.currentPassword.message}</p>
        )}
      </div>

      {/* New Password */}
      <div className="space-y-2">
        <Label htmlFor="newPassword">ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏</Label>
        <div className="relative">
          <Input
            id="newPassword"
            type={showNewPassword ? 'text' : 'password'}
            autoComplete="new-password"
            placeholder="ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            {...register('newPassword')}
            aria-invalid={!!errors.newPassword}
            className="pr-10"
          />
          <button
            type="button"
            onClick={() => setShowNewPassword(!showNewPassword)}
            className="text-muted-foreground hover:text-foreground absolute top-1/2 right-3 -translate-y-1/2 transition-colors"
            aria-label={showNewPassword ? 'ÎπÑÎ∞ÄÎ≤àÌò∏ Ïà®Í∏∞Í∏∞' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥¥Í∏∞'}
          >
            {showNewPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
          </button>
        </div>
        {errors.newPassword && (
          <p className="text-destructive text-sm">{errors.newPassword.message}</p>
        )}
        {/* Real-time password strength indicator */}
        {newPassword && <PasswordStrengthIndicator password={newPassword} />}
      </div>

      {/* Confirm Password */}
      <div className="space-y-2">
        <Label htmlFor="confirmPassword">ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</Label>
        <div className="relative">
          <Input
            id="confirmPassword"
            type={showConfirmPassword ? 'text' : 'password'}
            autoComplete="new-password"
            placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Îã§Ïãú ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            {...register('confirmPassword')}
            aria-invalid={!!errors.confirmPassword}
            className="pr-10"
          />
          <button
            type="button"
            onClick={() => setShowConfirmPassword(!showConfirmPassword)}
            className="text-muted-foreground hover:text-foreground absolute top-1/2 right-3 -translate-y-1/2 transition-colors"
            aria-label={showConfirmPassword ? 'ÎπÑÎ∞ÄÎ≤àÌò∏ Ïà®Í∏∞Í∏∞' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥¥Í∏∞'}
          >
            {showConfirmPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
          </button>
        </div>
        {errors.confirmPassword && (
          <p className="text-destructive text-sm">{errors.confirmPassword.message}</p>
        )}
      </div>

      {/* Security Notice */}
      <div className="rounded-lg border border-amber-200 bg-amber-50 p-3 dark:border-amber-900/50 dark:bg-amber-950/20">
        <p className="text-sm text-amber-800 dark:text-amber-200">
          üîí Î≥¥ÏïàÏùÑ ÏúÑÌï¥ ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Îã§Î•∏ Î™®Îì† Í∏∞Í∏∞ÏóêÏÑú ÏûêÎèôÏúºÎ°ú Î°úÍ∑∏ÏïÑÏõÉÎê©ÎãàÎã§.
        </p>
      </div>

      {/* Actions */}
      <div className="flex gap-2 pt-2">
        {onCancel && (
          <Button type="button" variant="outline" onClick={onCancel} disabled={isSubmitting}>
            Ï∑®ÏÜå
          </Button>
        )}
        <Button type="submit" disabled={isSubmitting} className="flex-1">
          {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
          {isSubmitting ? 'Î≥ÄÍ≤Ω Ï§ë...' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω'}
        </Button>
      </div>
    </form>
  );
}
